// class  sub  
.product-area .product-list .thumb{
  a{
    border: 1px solid #e6e6e6;
  }
  img{
    top: 0;
    left: 0;
    transform: translate(0, 0);
    &:hover{
      transform: scale(1.1);
    }
  }
}
